.ProductList {width: 100%!important;}
.ProductList ul {-ms-flex-wrap: wrap;flex-wrap: wrap;}
.ProductList .container {margin: 1.8rem 1rem;}
.ProductList .button {background: rgba(100%, 100%, 100%, 0.5); padding: 1.2rem 0.6rem; margin: 0.6rem 0.46rem; cursor: pointer;
    border-width: 2px; border-style: solid; border-color: rgba(180, 180, 180, 0.1); -webkit-box-sizing: border-box; box-sizing: border-box;}
.ProductList .icon img{ border-width: 0.5px; border-style: solid; border-color: rgba(200, 200, 200, 0.5); border-radius: 1.2rem; width:9.5rem; height: 9.5rem;}
.ProductList .title {font-size: 1.2rem; margin-top: 0.6rem;margin-bottom: 0.1rem;color:#667;white-space: nowrap;overflow: hidden;-o-text-overflow: ellipsis;text-overflow: ellipsis; width:10rem;}
.ProductList .subtitle {font-size: 0.8rem; color: #aaa;}



