.App {
  text-align: center;
  margin:0 auto;
  background: #eeeeee;
}

.AppContent {
  font-size: large;
  min-height: 15rem;
}

html{font-size:10px}
body,html{margin:0;padding:0;background-color:#fff;color:#333;font-family:Roboto,Helvetica,sans-serif}
@media only screen and (min-width:300px){html{font-size:8px!important}}
@media only screen and (min-width:401px){html{font-size:12px!important}}
@media only screen and (min-width:428px){html{font-size:13px!important}}
@media only screen and (min-width:481px){html{font-size:15px!important}}
@media only screen and (min-width:569px){html{font-size:17px!important}}
@media only screen and (min-width:641px){html{font-size:18px!important}}
@media only screen and (min-width:780px){html{font-size:20px!important}}
@media (min-width:375px){.App{width:375px}}
@media (min-width:768px){.App{width:750px}}
@media (min-width:992px){.App{width:970px}}
@media (min-width:1200px){.App{width:1170px}}

ul{list-style-type: none; display: -ms-inline-flexbox; display: inline-flex; -webkit-margin-before: 0px; -webkit-margin-after: 0px; -webkit-padding-start: 0px; -webkit-padding-end:0px;}

