
.Footer{background:#333;color:#fff;font-size:14px;font-weight:200;font-size-adjust: 0.35}
.Footer .container{margin:0 auto;padding:0.6rem 0px;max-width:1000px;text-align:center}

.Footer ul{list-style-type: none;display: -ms-inline-flexbox;display: inline-flex;-webkit-margin-before: 0px;-webkit-margin-after: 0px;-webkit-margin-start: 0px;-webkit-margin-end: 0px;-webkit-padding-start: 0px;}
.Footer li{padding-left: 2rem;padding-right: 2rem;}
.Footer a{padding:0.1rem 0.3rem;font-size:0.9rem;color:#bbb;text-decoration:solid;}
.Footer a,a:active,a:visited{color:hsla(0,0%,100%,.6);}
.Footer a:hover{color:#fff}

.Footer .copyright{margin-top: 2rem; color:#888; font-weight:60;font-size:0.8rem;}



