
@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.Header{background:#374257;color:#fff;height: 4rem;padding: 0;}
.Header .container{margin:0 auto;padding:0;max-width:1000px;}
.Header .brand{float:left;text-decoration:none;font-size:1.75em;line-height:4rem;height:4rem;}
.Header .brand .icon{-webkit-animation: App-logo-spin infinite 20s linear;animation: App-logo-spin infinite 20s linear;height: 4rem;margin-left:-5px;margin-right:-12px;}
.Header .brand .text{float:right;color:#92e5fc;}

.Header .nav{float:right;line-height:4rem;height:4rem;}
.Header .nav ul{-webkit-margin-before: 0px;-webkit-margin-after: 0px;}
.Header .nav a{display:inline-block;padding:0px 0.5rem;text-decoration:none;font-size:1.125em;margin:0px;}
.Header .nav a, a:active,a:visited{color:#ccc}
.Header .nav a:hover{color:#fff}

.Header .nav .current {opacity: 0.5;color:#ccc}
.Header .nav .settings{line-height:2rem;height:2rem; font-size: 0.8rem; font-weight: 100; text-align: right;}
.Header .nav .settings a{cursor: pointer;}
.Header .nav .settings ul{height:2rem;-webkit-padding-start: 1rem;-webkit-padding-end:0px;}

.Header .nav .tab-links{line-height:2rem;height:2rem;}
.Header .nav .tab-links ul{height:2rem;-webkit-margin-before: -3px;-webkit-padding-start: 1rem;-webkit-padding-end:1rem;}

.Header .nav a.active{border-radius:3px;background:rgba(50%,99%,80%,.15);color:#fff;border-top: 3px solid rgba(50%,99%,80%,1)}
.Header .nav a.active:hover{background:rgba(0,0,0,.3)}

